import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { FontColor } from 'external/types/common';
import {
  withLinkMemo,
  LinkWrapperProps,
  DEFAULT_PROPS as DEFAULT_PROPS_BY_LINK
} from 'external/src/Link';
import { getStoryComponent } from 'shared/storyHelper';

import styles from './styles.scss';

const cd = cssDecorator(styles);

export type MarkerFontWeight =
  'black'
  | 'semibold'
  | 'medium';

// Заготовка на случай расширения набора цветов для отдельных компонентов
export type MarkerFontColor = FontColor;

export interface MarkerPropsBase {
  /** Насыщенность шрифта */
  fontWeight?: MarkerFontWeight;

  /** Цвет шрифта */
  color?: MarkerFontColor;

  /** ID для тестирования */
  dataTestId?: string;

  /** Дочерние элементы */
  children: React.ReactNode;

  /** Флаг для отрисовки строчного представления */
  inline?: boolean;

  /** Флаг для наследования цвета текста от родителя */
  inheritColor?: boolean;
}

export const DEFAULT_PROPS = {
  color: 'primary',
  fontWeight: 'medium',
  inline: false,
  inheritColor: false
};

const Marker: React.FC<MarkerPropsBase> = ({
  fontWeight = DEFAULT_PROPS.fontWeight,
  color = DEFAULT_PROPS.color,
  inline = DEFAULT_PROPS.inline,
  inheritColor = DEFAULT_PROPS.inheritColor,
  dataTestId,
  children
}) => {
  const markerClasses = cd(
    'marker',
    {
      inline,
      [`font_weight_${ fontWeight }`]: true,
      [`color_${ color }`]: !inheritColor
    }
  );

  return (
    <div data-test-id={ dataTestId } className={ markerClasses }>
      { children }
    </div>
  );
};

export const StoryComponent = getStoryComponent<MarkerPropsBase, LinkWrapperProps>(
  DEFAULT_PROPS as MarkerPropsBase,
  DEFAULT_PROPS_BY_LINK as LinkWrapperProps
);

export default withLinkMemo(Marker);

export type MarkerProps = MarkerPropsBase & LinkWrapperProps;
